import {
  Card,
  Col,
  // Container,
  Row,
  CardBody,
  CardImg,
  // CardHeader,
  // CardTitle,
  // FormGroup,
  // Label,
  // Button,
  // Form,
  Input,
  // InputGroupAddon,
  // InputGroupText,
  // InputGroup,
  // Breadcrumb,
  // BreadcrumbItem,
} from "reactstrap"
// import CIcon from '@coreui/icons-react'

import logo from '../../../src/assets/ot.png'
import conf from '../../conf.js';

import { CContainer, CFade } from '@coreui/react'

// import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';

// import url from '../../conf.js';
// import { storage } from "../../firebase/firebase";
import $ from 'jquery'

// import ville from '../dict/ville.js'
// import Flatpickr from "react-flatpickr"
// import { French } from "flatpickr/dist/l10n/fr.js";

const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

class MesReservations extends Component {

  constructor(props) {
    super(props)
    this.state = {
     reservation:[]
    }
  }
  
  componentDidMount() {
   
    let  data=  JSON.parse(localStorage.getItem('token_auth'))
    $("#chargement").css("display","none")
   
   const requestOptions = {
    headers: { 
      'Authorization':'Bearer '+data.token,

    },

    'method':'GET'
   };

   fetch(conf.url+"user/users/"+data.user.id,requestOptions)
   .then((response) => response.json())
   .then((data) => {
     console.log("data",data)
    try{
      this.setState({reservation:data.reservations})
    }catch(error){
      this.setState({reservation:[]})
    }
      

     })

  }

  render() {

    // const animatedComponents = makeAnimated();
    // const { pick } = this.state
    
    // const test = [
    //   {
    //     options: [
    //       { label: "Test", value: "Test" },
    //       { label: "Test", value: "Test" },
    //       { label: "Test", value: "Test" }
    //     ]
    //   },
    // ]

    return (
      <>

      <CContainer fluid>
      <Suspense fallback={loading}>
      <CFade>

        <section id="contact" class="contact">
          <div class="container">

            <div>
              <div class="section-title">
                <h2>Mes Réservations</h2>
              </div>
            </div>
            {

              
       this.state.reservation.map((element)=>(

            <Row> 
              <Card>
                <CardBody>

                  <Row>
                    
                    <Col md="6">
                      <Row>
                        <CardImg top width="90%" src={logo} alt="Photo du térrain" />
                      </Row>

                      
                    </Col>

                    <Col md="6" className="mt-5"> 
                      <Row>
                        <Col md="12">
                          <label>Nom du térrain</label> 
                          <Input
                            className="form-control"
                            type="text"
                            id="nomterrain"
                           
                            value={element.nomTerrain}
                            readOnly
                          />
                        </Col>
                      </Row>

                    
                      <Row className="mt-3">
                        <Col md="12">
                          <label>Date de réservation</label> 
                          <Input
                            className="form-control"
                            type="text"
                            id="datereservation"
                            placeholder="Date de réservation"
                            value={element.dateReservation}
                            readOnly
                          />
                        </Col>
                      </Row>

                      <Row className="mt-3">
                        <Col md="12">
                          <label>Heure(s) de réservation</label> 

                          <ul class="ks-cboxtags">
                            {
                              element.heure.split(";").map((element,idx)=>(
                                (element!="")?
                                <li className="mr-2"><input type="checkbox"  id={"checkbox"+idx} checked value={element} /><label for={"checkbox"+idx}>{element}</label></li>:null
                                
                              ))
                            }
                   
                          </ul>
                         
                        </Col>
                      </Row>

                      {/* <Row className="mt-5">
                        <div class="text-center">
                          <Button id="annulerreservation"  color="danger" className="b-lg" >
                            Annuler la réservation
                          </Button>
                        </div>
                      </Row> */}

                    </Col>
                  </Row>

                </CardBody>
              </Card>
            </Row> 
             ))
           }

          </div>
        </section>

      </CFade>
      </Suspense>
      </CContainer>

      </>
    )
  }
}

export default MesReservations
